<template>
    <div id="articleList">
        <articles v-for='id in articleIdList' :key='id' :id='id'></articles>
    </div>
</template>
<script>
import articles from '@/components/articles';
export default {
    name:'articleList',
    components:{
        articles
    },
    data(){
        return{
            articleIdList:null
        }
    },
    created(){
        this.getLatestArti()
    },
    methods:{
        getLatestArti(){
                this.axios({
                    url:'http://127.0.0.1:8000/api/getLatestArti',
                    method:'get',
                })
                .then(res=>{
                    this.articleIdList=res.data.articleIdList;
                    })
                .catch(error=>{console.log(error)})
            }
        }

}

</script>
<style scoped>
#articleList{
    height: fit-content;
    width: 85%;
    padding: 2em 0;
}
</style>


